import React, { useState, useEffect } from "react";
import {
  Stack,
  Box,
  useMediaQuery,
  
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import { useNavigate, useLocation } from "react-router-dom";
import ConnectButton from "./ConnectButton";

const MotionBox = motion(Box);

function NavMenu(props) {
  const [isPC] = useMediaQuery("(min-width: 750px)");
  
  const { pathname } = useLocation();
  const navigate = useNavigate();

  const NavMenu = (props) => {
    return (
      <MotionBox
        cursor="pointer"
        _hover={{
          color: "rgb(255,69,0)",
        }}
        onClick={() => {
          navigate(props.link);
        }}
      >
        {props.name}
      </MotionBox>
    );
  };

  return (
    <Stack
      direction={isPC ? "row" : "column"}
      spacing={isPC ? "35px" : ""}
      fontWeight="medium"
      fontSize={isPC ? "large" : "2xl"}
      justify="center"
      align="center"
    >
      <NavMenu name="Home" link="/" click={props.click} />
      <NavMenu name="Martianomics" link="/tokenomics" click={props.click} />
      <NavMenu name="Team" link="/Team" click={props.click} />
      {pathname != "/MartianSwap" ? (
        <NavMenu
          name="Be an Early Believer"
          link="/MartianSwap"
          click={props.click}
        />
      ) : (
        <ConnectButton />
      )}
    </Stack>
  );
}

export default NavMenu;
