import MartianSwap from "../components/ui-components/MartianSwap";
import QRCodePay from "../components/ui-components/QRCodePay";
import HowToBuy from "../components/ui-components/HowToBuy";
import { VStack, Image } from "@chakra-ui/react";
import React, {Suspense} from "react"
import logotothemars_trans from "../components/img/logo_round_large.png";

const MartianSwapPage = () => {
    return (
      <Suspense
        fallback={
          <Image
            src={logotothemars_trans}
            href="www.tothemars.in"
            boxSize="100px"
            objectFit="cover"
          ></Image>
        }
      >
        <VStack>
          <MartianSwap />
          <QRCodePay />
          <HowToBuy />
        </VStack>
      </Suspense>
    );
}

export default MartianSwapPage